import React, { Component } from 'react';
import {
  Image, Text, View, Linking, CheckBox, ScrollView,
} from 'react-native';
import styled from 'styled-components';

const fontSize = {
  low: '15px',
  medium: '18px',
  high: '24px',

};
const fontWeight = {
  low: '200',
  medium: '600',
  high: '800',
};
const fontFamily = '\'Osaka-Mono\'';
const Container = styled(ScrollView)`
 flex: 1;
`;
const Contents = styled(View)`
  align-items: center;
  background-color:#e6e6e6;
  padding-bottom:30px;
`;
const SkillSet = styled(View)`
  flex-direction: row;
  align-items:center;
`;
const LanguageCheckBox = styled(CheckBox)`
  margin-left:8px;
  margin-right:8px;
  margin-top:2px;
`;
const SkilSetLanguageView = styled(View)`
  flex-direction: row;
  padding-top: 10px;
  padding-bottom: 10px;
  justify-content: start;
`;
const SkilSetLanguage = styled(Text)`
  font-size: ${fontSize.medium};
`;
const SkilSetStatusView = styled(View)`

`;
const SkilSetStatus = styled(Text)`
  font-size: ${fontSize.medium};
  padding-left:10px;
  padding-right:10px;
`;
const SkillSetFrameWork = styled(Text)`
  font-size: ${fontSize.low};
  padding-right:5px;
  padding-left:5px;
`;
const ProfileText = styled(Text)`
  font-size: ${fontSize.medium};
  letter-spacing: 0.12em;
  margin-bottom:20px;
  margin-left:10px;
  margin-right:10px;    
`;
const Header = styled(View)`
  align-items: center;
`;
const HeaderText = styled(Text)`
  margin-top:25px;
  font-size: ${fontSize.high};
  font-weight: ${fontWeight.high};
  font-family: ${fontFamily};
`;
const HeaderImage = styled(Image)`
  height: 200px;
  width: 200px;
  margin-top:50px;
  margin-bottom:50px;
  border-radius:50px;

`;
const profileText = `
I currently live in the Tokyo with my wife and young son. I was born and raised in Sapporo.
I like \`React Native\` and \`React Native for Web\`. I have way too many coffee brewing devices.
`;
const skillSet = [
  { HTML: [] },
  { CSS: [] },
  { JavaScript: ['React', 'React Native', 'Redux'] },
  { Swift: [], status: '' },
  { Python: [], status: '' },
  { 'Objective-C': [], status: '' },
];
const Profile = styled(View)``;
const SocialLinkView = styled(View)`
  flex-direction: row;
  margin-top:20px;
`;


const LinkText = styled(Text)`
  font-size: ${fontSize.medium};
  color: #1B95E0;
  background-color: white;
  margin-left:10px;
  margin-right:10px;
  padding:10px;
  border-radius:20px;
`;

const BoldBigText = styled(Text)`
  font-size:44px;
  font-weight:900;
`;
const Link = ({ href, children }) => (
  <LinkText
    target="_blank"
    href={href}
    onPress={() => {}}
    accessibilityRole="link"
  >
    {children}
  </LinkText>
);

class App extends Component {
  render() {
    return (
      <Container>
        <Contents>
          <Header>
            <HeaderText>Welcome to MyPage! ( - _ - )</HeaderText>
            <HeaderImage source={{ uri: require('./assets/Image.jpg') }} />
          </Header>
          <Profile>
            <BoldBigText>ABOUT</BoldBigText>
            <ProfileText>
              {profileText}
            </ProfileText>
            <View>
              {skillSet.map((item, index) => {
                const Language = Object.keys(item)[0];
                const Status = item[Object.keys(item)[1]];
                const FrameWork = item[Language].map((item, index) => (
                  <SkillSetFrameWork
                    key={index}
                  >
                    {item}
                  </SkillSetFrameWork>
                ));
                return (
                  <SkillSet key={index}>
                    <SkilSetLanguageView>
                      <LanguageCheckBox
                        value
                      />
                      <SkilSetLanguage>{Language}</SkilSetLanguage>
                      <SkilSetStatusView>
                        {Status !== undefined
                                                && <SkilSetStatus>{Status}</SkilSetStatus>}
                      </SkilSetStatusView>
                    </SkilSetLanguageView>
                    {FrameWork}
                  </SkillSet>
                );
              })}
            </View>

          </Profile>
          <BoldBigText>SOCIAL</BoldBigText>
          <SocialLinkView>
            <Link href="https://twitter.com/mxcn3">Twitter</Link>
            <Link href="https://github.com/yushimatenjin">GitHub</Link>
          </SocialLinkView>
        </Contents>
      </Container>
    );
  }
}

export default App;



